	.shadow_2		- box shadow
	.shadow_3		- box shadow
	.shadow_4		- box shadow
	.shadow_5		- box shadow

	Markup:
	<div class="{{modifier_class}} p_3 br_solid br_black-1 br_1 c_whtie">container has shadow of {{modifier_class}}</div>

