  <span class="a:bg_accent bg_shade-2 font_4 text_center br_1 br_solid br_shade-4 p_4">Click to Toggle parent is-active</span>
  <span class="a:{{modifier_class}} a:bg_accent bg_shade-2 font_4 text_center br_1 br_solid br_shade-4 p_4">Span with display:{{modifier_class}} when parent is active</span>
</div>

Weight: 10

Styleguide is-active.display

*/
/*
UC: Animation

Transitions Eases and Transforms.Create simple toggleable animations with a combination of classes. 

Weight: 140

Styleguide Animation


*/
/*
Transition on Hover 

soften the harshness of the switch of the style by adding transitions to the changed decorators.

0              - 0 second transition
1              - .1 second transition
2              - .25 second transition
3              - .3 second transition
4              - .5 second transition
5              - 1 second transition

Markup:
<div class="flex m_5 bg_primary m_5">
  <div class="transition_{{modifier_class}} h:bg_black-0  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">0</div>
  <div class="transition_{{modifier_class}} h:bg_black-1  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">1</div>
  <div class="transition_{{modifier_class}} h:bg_black-2  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">2</div>
  <div class="transition_{{modifier_class}} h:bg_black-3  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">3</div>
  <div class="transition_{{modifier_class}} h:bg_black-4  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">4</div>
  <div class="transition_{{modifier_class}} h:bg_black-5  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">5</div>
  <div class="transition_{{modifier_class}} h:bg_black-6  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">6</div>
  <div class="transition_{{modifier_class}} h:bg_black-7  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">7</div>
  <div class="transition_{{modifier_class}} h:bg_black-8  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">8</div>
  <div class="transition_{{modifier_class}} h:bg_black-9  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">9</div>
  <div class="transition_{{modifier_class}} h:bg_black  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">default</div>
</div>


Styleguide Animation.transition

 */
*[class*=transition] {
  transition-timing-function: ease;
  transition-property: all;
}

.transition_0 {
  transition: all 0s;
}

.transition_1 {
  transition: all 0.1s;
}

.transition_2 {
  transition: all 0.2s;
}

.transition_3 {
  transition: all 0.35s;
}

.transition_4 {
  transition: all 0.5s;
}

.transition_5 {
  transition: all 1s;
}

/*
Easing the on Hover 

Dynamic adjustment of transition with easing.

out-in              - ease timing
out              - ease-out timing
in              - ease-in timing
in-out              - ease-in-out timing
linear              - linear timing


Markup:
<div class="flex m_5 bg_primary m_5">
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-0  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">0</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-1  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">1</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-2  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">2</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-3  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">3</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-4  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">4</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-5  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">5</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-6  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">6</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-7  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">7</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-8  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">8</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black-9  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">9</div>
  <div class="transition_3 ease_{{modifier_class}} h:bg_black  p-y_3 p-x_4 flex_auto font_bold font_2 font_slab text_center br_1 br_solid br_shade-4 ">default</div>
</div>


Styleguide Animation.ease

 */
.ease_linear {
  transition-timing-function: linear;
}

.ease_out-in {
  transition-timing-function: ease;
}

.ease_in {
  transition-timing-function: ease-in;
}

.ease_out {
  transition-timing-function: ease-out;
}

.ease_in-out {
  transition-timing-function: ease-in-out;
}

/*
Transform Scale 

Dynamic adjustment of objects scale for toggling between

0              - Scale to 0%
100            - Scale to 100%
X-0            - Scale the X to 0%
X-100            - Scale the X to 100%
Y-0            - Scale the X to 0%
Y-100            - Scale the X to 100%

Markup:
<div class="flex m_5 bg_primary m_5">
  <div class="scale_{{modifier_class}} w-10 p-y_3 p-x_4 font_bold font_2 font_slab text_center br_1 br_solid br_shade-4">This is content that will be scaled.</div>
</div>

Styleguide Animation.ease

 */
.scale_X-0 {
  transform: scaleX(0);
}

.scale_X-100 {
  transform: scaleX(1);
}

.scale_Y-0 {
  transform: scaleY(0);
}

.scale_Y-100 {
  transform: scaleY(1);
}

.scale_0 {
  transform: scale(0, 0);
}

.scale_100 {
  transform: scale(1, 1);
}

.origin_tl {
  transform-origin: left top;
}

.origin_rt {
  transform-origin: right top;
}

.origin_c {
  transform-origin: center center;
}

.origin_lb {
  transform-origin: left bottom;
}

.origin_rb {
  transform-origin: right bottom;
}

.origin_t {
  transform-origin: center top;
}

.origin_b {
  transform-origin: center bottom;
}

.origin_r {
  transform-origin: right center;
}

.origin_l {
  transform-origin: left center;
}

/*
UC: Counters

Add counters infront of collections without the need of an OL or UL. Counters cannot be nested!

Weight: 150

Styleguide counters

*/
/* No styleguide reference. */
/*
	Counter Basics

	Counter can be incremented by the `counter_increment` class.  

	Markup:
    <div class="counter_reset">
        <div class="bg_primary-2 p_3 m_3"><span class="font_bold counter counter_increment">:</span> Item, Object, or Section</div>
        <div class="bg_primary-2 p_3 m_3"><span class="font_bold counter counter_increment">:</span> Item, Object, or Section</div>
        <div class="bg_primary-2 p_3 m_3"><span class="font_bold counter counter_increment">:</span> Item, Object, or Section</div>
 	</div>

	 Weight: -100

	Styleguide counters.basics

*/
.counter_reset {
  counter-reset: a;
}

.counter_increment {
  counter-increment: a;
}

.counter:before {
  content: counter(a);
}

/*
	Counter Sub

	Counter Subs can be nested inside of counters

	Markup:
    <div class="counter_reset ">
        <div class="bg_primary-2 p_3 m_3"><span class="font_bold counter counter_increment counter_upper-alpha"></span> Item, Object, or Section</div>
            <div class="counter-sub_reset">
                <div class="m_3 p_2 p-l_4 bg_primary-3"><span class="font_bold counter counter_upper-alpha"></span>.<span class="font_bold counter-sub counter-sub_increment counter-sub_lower-alpha"></span> Sub Item</div>
                <div class="m_3 p_2 p-l_4 bg_primary-3"><span class="font_bold counter counter_upper-alpha"></span>.<span class="font_bold counter-sub counter-sub_increment counter-sub_lower-alpha"></span> Sub Item</div>
                <div class="m_3 p_2 p-l_4 bg_primary-3"><span class="font_bold counter counter_upper-alpha"></span>.<span class="font_bold counter-sub counter-sub_increment counter-sub_lower-alpha"></span> Sub Item</div>
            </div>
        <div class="bg_primary-2 p_3 m_3"><span class="font_bold counter counter_increment counter_upper-alpha"></span> Item, Object, or Section</div>
        <div class="bg_primary-2 p_3 m_3"><span class="font_bold counter counter_increment counter_upper-alpha"></span> Item, Object, or Section</div>
 	</div>

	Styleguide counters.sub counters

*/
.counter-sub_reset {
  counter-reset: b;
}

.counter-sub_increment {
  counter-increment: b;
}

/*
	Counter Types

	Create different types of lists with the counter types

	counter 										- Defualt Number Listing
	counter_lower-alpha         - Lowercase Alphabetic Listing
	counter_upper-alpha         - Uppercase Alphabetic Listing
	counter_lower-roman         - Lowercase Roman Listing
	counter_upper-roman         - Uppercase Roman Listing

	Markup:
	<div class="counter_reset">
			<div class="m_3 p_3 bg_primary-2"><span class="{{modifier_class}} font_bold counter_increment"></span> Item, Object, or Section</div>
			<div class="m_3 p_3 bg_primary-2"><span class="{{modifier_class}} font_bold counter_increment"></span> Item, Object, or Section</div>
			<div class="m_3 p_3 bg_primary-2"><span class="{{modifier_class}} font_bold counter_increment"></span> Item, Object, or Section</div>
 	</div>

	Styleguide counters.types

*/
.counter_lower-alpha:before {
  content: counter(a, lower-alpha);
}

.counter_upper-alpha:before {
  content: counter(a, upper-alpha);
}

.counter_lower-roman:before {
  content: counter(a, lower-roman);
}

.counter_upper-roman:before {
  content: counter(a, upper-roman);
}

.counter-sub:before {
  content: counter(b);
}

.counter-sub_lower-alpha:before {
  content: counter(b, lower-alpha);
}

.counter-sub_upper-alpha:before {
  content: counter(b, upper-alpha);
}

.counter-sub_lower-roman:before {
  content: counter(b, lower-roman);
}

.counter-sub_upper-roman:before {
  content: counter(b, upper-roman);
}

/*
	Order Markers

	Quickly decorate the counter with a circle.  Change the  bgground and font color with utility classes to acheive different effects.


	Markup:
    <div class="counter_reset">
        <div class="m_3 p_3"><span class="font_bold counter counter_increment order-marker bg_primary-2"></span> Item, Object, or Section</div>
        <div class="m_3 p_3"><span class="font_bold counter counter_increment order-marker font_10 br-tr_round br-br_round bg_highlight-2"></span> Item, Object, or Section</div>
        <div class="m_3 p_3"><span class="font_bold counter counter_increment order-marker bg_info-2"></span> Item, Object, or Section</div>
        <div class="m_3 p_3"><span class="font_bold counter counter_increment order-marker bg_warning-2"></span> Item, Object, or Section</div>
    </div>

	Weight: 100

	Styleguide counters.markers

*/
.order-marker {
  display: inline-block;
  height: 1.5em;
  width: 1.5em;
  margin-right: 0;
  margin-bottom: 0;
  padding: 0.75em 0 0 0;
  position: relative;
  margin-right: 0.5em;
  text-align: center;
  box-sizing: border-box;
  line-height: 0;
  vertical-align: top;
}